import React  from 'react';
import { withRouter } from 'react-router';
import { connect } from "react-redux";
import { Row, Col, message, Table, Icon, Dropdown, Menu, Tree,Tooltip, } from 'antd';
import ReactJson from 'react-json-view';
import Markdown from 'react-markdown';
import {CopyToClipboard} from 'react-copy-to-clipboard';
import './index.scss';

import { default as APICallsActions } from "store/apicalls/actions";
// components
import Loader from 'components/loader';
import * as Utils from 'utils/util';
import { pathConstants } from 'constants/path-constants';
// constants
const APP_CONSTANTS = require('../../constants/app-constants')
const { getAPICallsData } = APICallsActions;
const DirectoryTree = Tree.DirectoryTree;
const { TreeNode } = Tree;
// const Panel = Collapse.Panel;

class APICall extends React.Component {
    constructor(props) {
        super(props);

       this.state = {
            showloading:false,
            showHeader:false,
            ApiRequest:{},
            folders:{},
            selectedList:{},
            selectedListResponse:{},
            selectedResponse:{},
            showResponse:false,
            dropdownText:"Example Response",
            bodyBeautifier:false,
            bodyCollapse:false,
            requestBeautifier:false,
            requestCollapse:false,
            responseBeautifier:true,
            responseCollapse:false,
       }
    
    this.changeRequestView =  this.changeRequestView.bind(this);
    this.collapseRequestJson  = this.collapseRequestJson.bind(this);
    this.changeResponseView =  this.changeResponseView.bind(this);
    this.collapseResponseJson  = this.collapseResponseJson.bind(this);
    this.changeBodyView =  this.changeBodyView.bind(this);
    this.collapseBodyJson  = this.collapseBodyJson.bind(this);
    this.copyToJson  = this.copyToJson.bind(this);
    }

    componentDidMount(){
        debugger;
        this.setState({showloading:true});
      
        this.props.getAPICallsData();
    }

    componentWillReceiveProps(nextProps){
        // console.log('nextProps'+JSON.stringify(nextProps));
        // console.log('nextProps'+JSON.stringify(nextProps.getAPIData));
        if(nextProps && nextProps.getAPIData && nextProps.getAPIData !== this.props.getAPIData){
            if(nextProps.getAPIData.type !== "GET_APICALL_DATA_ERROR_RESULT"){
                this.setState({ApiRequest:nextProps.getAPIData.requests,
                dropdownText:nextProps.getAPIData.requests[0].responses.length>0?nextProps.getAPIData.requests[0].responses[0].name:"alpha",
                selectedList:nextProps.getAPIData.requests[0],
                selectedListResponse:nextProps.getAPIData.requests[0].responses,
                selectedResponse:nextProps.getAPIData.requests[0].responses[0],
                folders:nextProps.getAPIData.folders
            });
            }else{
                
                message.error("API failed");
                this.props.history.push(pathConstants.BLOCKEXPLORER);
                Utils.goToTop();
               
               
            }
            this.setState({showloading:false});
        }
        // console.log('state'+this.state.ApiRequest);
    }

    selectedList = (record) =>{
        debugger;
        // console.log('record'+record.responses);
        this.setState({ selectedList: record,
            showResponse:true,
            selectedListResponse: record.responses.length>0? record.responses:null,
            selectedResponse:record.responses.length>0 ?record.responses[0]:null,
            dropdownText:record.responses.length>0?record.responses[0].name:"Response",
         }, function () {
            // console.log(this.state.selectedResponse);
        });
    }
   
    responseMenuSelected = (e) => {
        debugger;
        console.log('record'+e.key);
        console.log('res'+this.state.selectedListResponse[e.key]);
        this.setState({ 
            dropdownText:this.state.selectedListResponse[e.key].name?this.state.selectedListResponse[e.key].name:"Response",
            selectedResponse:this.state.selectedListResponse[e.key]}, function () {
            // console.log(this.state.selectedResponse);
        });
    }
    
  

    getChildTreeNodes = (data) => {
        var tempArr = data.name;
        tempArr =[];
     
        for(var i in this.state.ApiRequest){
            if(this.state.ApiRequest[i].folder === data.id){
                tempArr.push(
                    <TreeNode title={this.state.ApiRequest[i].name} key={i} isLeaf/>
                )
            }
        }
        return tempArr;
    }

    getSubChildNodes = (data) => {
        var tempChildFolder  = [];
        var tempChildNodes = [];
        // var count = data.order.length-1;
        // console.log('count'+count);
       
        for(var i in data.order){
            for(var j in this.state.ApiRequest){
                debugger;
                if(data.order[i] === this.state.ApiRequest[j].id){
                    tempChildNodes.push(
                        // <TreeNode title={data.name}>
                            <TreeNode title={this.state.ApiRequest[j].name} isLeaf/>
                        // </TreeNode>
                    )
                }
            }
        }
        tempChildFolder.push(
            <TreeNode className="api-call__mainDir" title={data.name}>
                 {tempChildNodes}
            </TreeNode>
        )
        // console.log('child folder'+tempChildFolder);
        return tempChildFolder;
    }

    onSelect = (selectedKeys, info) => {
        var tempRequest = {};
        console.log('selected keys', selectedKeys);
        if(selectedKeys[0] !== "0-0"){
            for(var index in this.state.ApiRequest){
                if(this.state.ApiRequest[index].name === info.node.props.title){
                    // console.log('Selected name'+this.state.ApiRequest[index].name)
                    // console.log('selected name', info.node.props.title);
                    tempRequest =  this.state.ApiRequest[index];
                    Utils.goToTop();
                    // console.log('selected item'+JSON.stringify(tempRequest.responses));
                   this.setState({
                       selectedList:tempRequest,
                       selectedResponse:tempRequest.responses.length>0?tempRequest.responses[0]:null,
                       selectedListResponse:tempRequest.responses.length>0?tempRequest.responses:null,
                       dropdownText:tempRequest.responses.length>0?tempRequest.responses[0].name:"Response"
                   })
                }
            }     
      }    
    }

    getChildFolder  = (data) => {
        var tempArr = data.name;
        tempArr =[];

        if(data.folders_order.length>0){
            debugger;
            for(var i =0;i<data.folders_order.length;i++){
                for(let j in this.state.folders){
                    if(data.folders_order[i] === this.state.folders[j].id ){
                        tempArr.push(
                            <TreeNode title={this.state.folders[j].name}> 
                             {
                                      this.state.folders[j].folder ?
                                        this.getChildTreeNodes(this.state.folders[j])
                                      :
                                      <TreeNode className="api-call__mainDir" title={this.state.folders[j].name}  >
                                          {this.getChildFolder(this.state.folders[j])}
                                      </TreeNode>
                                  }                                
                                 {/* {this.getChildTreeNodes(this.state.folders[j])} */}
                            </TreeNode>
                        )
                    }
                }
            }
        }else{
            for(var j in this.state.ApiRequest){
                if(this.state.ApiRequest[j].folder === data.id){
                    tempArr.push(
                        <TreeNode title={this.state.ApiRequest[j].name} key={j} isLeaf/>
                    )
                }
            }  
        }
        return tempArr;
    }

    changeRequestView(){
        this.setState({
            requestBeautifier:!this.state.requestBeautifier
        });
    }

    collapseRequestJson(){
        this.setState({
            requestCollapse:!this.state.requestCollapse
        });
    }

    changeResponseView(){
        this.setState({
            responseBeautifier:!this.state.responseBeautifier
        });
    }

    collapseResponseJson(){
        this.setState({
            responseCollapse:!this.state.responseCollapse
        });
    }

    changeBodyView(){
        this.setState({
            bodyBeautifier:!this.state.bodyBeautifier
        });
    }

    collapseBodyJson(){
        this.setState({
            bodyCollapse:!this.state.bodyCollapse
        });
    }
    copyToJson (){
        message.success("Copied successfully");
    }



    render() {
        const headerData =[];
        const folderStr =[];
              
      
        for(var i in this.state.selectedList.headerData){
                headerData.push({
                    'key':this.state.selectedList.headerData[i].key,
                    'value':this.state.selectedList.headerData[i].value?this.state.selectedList.headerData[i].value:"-"
                })
        }
      
       for(var j in this.state.folders){  
            folderStr.push(
                     <DirectoryTree multiple
                                  onSelect={this.onSelect}
                                  defaultExpandAll={true}
                                  >
                                  {
                                      this.state.folders[j].folder ?
                                      null
                                      :
                                      <TreeNode className="api-call__mainDir" title={this.state.folders[j].name}  >
                                          {this.getChildFolder(this.state.folders[j])}
                                      </TreeNode>
                                  }
                    </DirectoryTree>
               )
       }
        
        const dropdownMenu = [];
        for(var k in this.state.selectedList.responses){
                dropdownMenu.push(
                        <Menu.Item key={k}>
                            <a>
                            {
                                this.state.selectedList.responses[k].name?
                                'Response -'+this.state.selectedList.responses[k].name
                                :'Response'+k
                            }</a>
                        </Menu.Item>
                )
        }
        const menu = (
            <Menu  onClick={this.responseMenuSelected}>{dropdownMenu}</Menu>
        )

        return (
            <div className="api-call">
            {
                !this.state.showloading?
               <Row>
                   <Col md={5} xs={24} className="api-call__first">
                        <div>
                                { folderStr }
                        </div>
                   </Col>
                   <Col md={19} xs={24} className="api-call__middle">
                        <div>
                            <h3>
                                <span className="api-call__method">{this.state.selectedList.method}</span>&nbsp;&nbsp;
                                <span>{this.state.selectedList.name}</span>
                            </h3>
                            <p className="api-call__middle__para">
                                {this.state.selectedList.url} 
                            </p>

                            <p className="api-call__para">
                                <Markdown source={this.state.selectedList.description}/>
                            </p>
                            <Row>
                            {
                                headerData.length>0?
                                <div>
                                    <h3>Header</h3>
                                    {/* {headerDOM} */}
                                    <Table {...this.state} dataSource={headerData} 
                                            columns={APP_CONSTANTS.APIDocsColumns}
                                            pagination={false} />
                                    <br /><br />
                                    {
                                        this.state.selectedList.rawModeData?
                                        <div>
                                        <Row className="api-call__iconheader">
                                                <Col span={12}>
                                                    <h3>Body - Request</h3>
                                                </Col>
                                                <Col span={12}>
                                                    <div className="u_float_right">
                                                        {
                                                            !this.state.bodyCollapse?
                                                                <Tooltip placement="top" title="hide">
                                                                    <Icon className="api-call__icon" type="down" onClick={this.collapseBodyJson}/>
                                                                </Tooltip>
                                                            :
                                                                <Tooltip placement="top" title="show">
                                                                    <Icon className="api-call__icon" type="up" onClick={this.collapseBodyJson}/>
                                                                </Tooltip>
                                                        }

                                                        {
                                                            !this.state.bodyBeautifier?
                                                               
                                                                <Tooltip placement="top" title="Beautifier">
                                                                        <div className="api-call__icon"  style={{display:"inline"}} onClick={this.changeBodyView}>
                                                                            <Icon className="api-call__beautifier" type="left"/>
                                                                            <Icon className="api-call__beautifier" type="right"/>
                                                                        </div>
                                                                </Tooltip>
                                                                :
                                                                <Tooltip placement="top" title="Beautifier">
                                                                    <div className="api-call__icon"  style={{display:"inline"}} onClick={this.changeBodyView}>
                                                                        <Icon  type="left"/>
                                                                        <Icon  type="right"/>
                                                                    </div>
                                                                </Tooltip>
                                                                // <img className="query-console-page__img" src={code} onClick={this.changeView}/>
                                                                
                                                        }

                                                         <CopyToClipboard text={this.state.selectedList.rawModeData} 
                                                                        onCopy={this.copyToJson}
                                                                        >
                                                            <Tooltip placement="top" title="copy">
                                                                    <Icon className="api-call__icon" type="copy" />
                                                            </Tooltip>
                                                        </CopyToClipboard>

                                                    </div>
                                                </Col>
                                            </Row>
                                        
                                            <Row className="api-call__requestContent">
                                            {
                                                !this.state.bodyCollapse?
                                              
                                                        !this.state.bodyBeautifier?
                                                            <ReactJson  src={JSON.parse(this.state.selectedList.rawModeData)} />
                                                        :
                                                            <p className="api-call__para api-call__middle__body ">{this.state.selectedList.rawModeData}</p>
                                                    
                                                :null
                                            }
                                            </Row>
                                           

                                          </div>
                                          
                                        :null
                                        
                                    }
                                </div>
                                :null
                            }
                            </Row> <br /><br />

                            {/* Request and response */}
                            <Row>
                                { 
                                    this.state.selectedList.rawModeData?
                                        <div >
                                            <Row className="api-call__iconheader">
                                                <Col span={12}>
                                                    <h3>Example Request</h3>
                                                </Col>
                                                <Col span={12}>
                                                    <div className="u_float_right">
                                                        {
                                                            !this.state.requestCollapse?
                                                                <Tooltip placement="top" title="hide">
                                                                    <Icon className="api-call__icon" type="down" onClick={this.collapseRequestJson}/>
                                                                </Tooltip>
                                                            :
                                                                <Tooltip placement="top" title="show">
                                                                    <Icon className="api-call__icon" type="up" onClick={this.collapseRequestJson}/>
                                                                </Tooltip>
                                                        }

                                                   

                                                         {
                                                            !this.state.requestBeautifier?
                                                               
                                                                <Tooltip placement="top" title="Beautifier">
                                                                        <div className="api-call__icon"  style={{display:"inline"}} onClick={this.changeRequestView}>
                                                                            <Icon className="api-call__beautifier" type="left"/>
                                                                            <Icon className="api-call__beautifier" type="right"/>
                                                                        </div>
                                                                </Tooltip>
                                                                :
                                                                <Tooltip placement="top" title="Beautifier">
                                                                    <div className="api-call__icon"  style={{display:"inline"}} onClick={this.changeRequestView}>
                                                                        <Icon  type="left"/>
                                                                        <Icon  type="right"/>
                                                                    </div>
                                                                </Tooltip>
                                                               
                                                                
                                                        }

                                                         <CopyToClipboard text={this.state.selectedList.rawModeData} 
                                                                        onCopy={this.copyToJson}
                                                                        >
                                                            <Tooltip placement="top" title="copy">
                                                                    <Icon className="api-call__icon" type="copy" />
                                                            </Tooltip>
                                                        </CopyToClipboard>

                                                    </div>
                                                </Col>
                                            </Row>
                                            <Row className="api-call__requestContent">
                                            {
                                                !this.state.requestCollapse?
                                              
                                                        !this.state.requestBeautifier?
                                                            <ReactJson  src={JSON.parse(this.state.selectedList.rawModeData)} />
                                                        :
                                                            <p className="api-call__para api-call__middle__body ">{this.state.selectedList.rawModeData}</p>
                                                    
                                                :null
                                            }
                                            </Row>
                                           
                                           
                                            
                                        </div>
                                    :null
                                }    
                            </Row><br /><br />
                            <Row>

                                  <Row className="api-call__iconheader">
                                        <Col span={12}>
                                            <Dropdown className="api-call__dropdown"
                                                    overlay={menu} 
                                                    trigger={['click']}
                                                    >
                                                <h3 className="ant-dropdown-link">
                                                    {this.state.dropdownText} <Icon type="down" />
                                                </h3>
                                            </Dropdown>
                                        </Col>
                                        <Col span={12}>
                                            <div className="u_float_right">
                                                {
                                                    !this.state.responseCollapse?
                                                        <Tooltip placement="top" title="hide">
                                                            <Icon className="api-call__icon" type="down" onClick={this.collapseResponseJson}/>
                                                        </Tooltip>
                                                    :
                                                        <Tooltip placement="top" title="show">
                                                            <Icon className="api-call__icon" type="up" onClick={this.collapseResponseJson}/>
                                                        </Tooltip>
                                                }

                                            

                                                    {
                                                    !this.state.responseBeautifier?
                                                        
                                                        <Tooltip placement="top" title="Beautifier">
                                                                <div className="api-call__icon"  style={{display:"inline"}} onClick={this.changeResponseView}>
                                                                    <Icon className="api-call__beautifier" type="left"/>
                                                                    <Icon className="api-call__beautifier" type="right"/>
                                                                </div>
                                                        </Tooltip>
                                                        :
                                                        <Tooltip placement="top" title="Beautifier">
                                                            <div className="api-call__icon"  style={{display:"inline"}} onClick={this.changeResponseView}>
                                                                <Icon  type="left"/>
                                                                <Icon  type="right"/>
                                                            </div>
                                                        </Tooltip>
                                                        // <img className="query-console-page__img" src={code} onClick={this.changeView}/>
                                                        
                                                }

                                                    <CopyToClipboard text={this.state.selectedResponse.text} 
                                                                onCopy={this.copyToJson}
                                                                >
                                                    <Tooltip placement="top" title="copy">
                                                            <Icon className="api-call__icon" type="copy" />
                                                    </Tooltip>
                                                </CopyToClipboard>

                                            </div>
                                        </Col>
                                    </Row>
                                    <Row className="api-call__requestContent">
                                            {
                                                !this.state.responseCollapse?
                                              
                                                       !this.state.responseBeautifier?
                                                             <ReactJson  src={JSON.parse(this.state.selectedResponse.text)} />
                                                        :
                                                            <p className="api-call__para api-call__middle__body ">{this.state.selectedResponse.text}</p>
                                                    
                                                :null
                                            }
                                            </Row>
                        </Row>
                           
                        </div>
                   </Col>
                   
               </Row>
               :
               <Loader />
            }
            </div>
        );
    }

}

function mapStateToProps(state) {
    return {
        ...state,
        getAPIData: state.apicalls.ApicallData, 
    }
}
export default connect(
    mapStateToProps,
    {
        getAPICallsData
    })(withRouter(APICall));

// export default withRouter(APICall);



