const APP_CONSTANTS = require('constants/app-constants');
const LineChartColor = [
    { color: "hsl(8,67%,82%)" },
    { color: "hsl(2,100%,67%)" },
    { color: "hsl(17,81%,72%)" },
    { color: "hsl(11,84%,65%)" },
    { color: "hsl(48,65%,63%)" },
]


export function getTitle(index) {
    switch (index) {
        case 0:
            return "INIT PROPOSAL"
            // break;
        case 1:
            return "CREATE PO"
        case 2:
            return "SEND INVOICE"
        case 3:
            return "PAYMENT"
        case 4:
            return "RECEIPT"
        
        default :
         return;
    }
}




export function userAction(receiptId, type) {
    var id;
    if (type === "Seller")
        id = APP_CONSTANTS.SELLER_ID;
    else
        id = APP_CONSTANTS.BUYER_ID;
    var data = {
        "userInfo":
        {
            "id": id
        },
        "tranactionData":
        {
            "proposal": "resource:param.network.model.ParamReceipt#" + receiptId,
        }
    }
    return data;
}


export function formatDate(date) {
    // var d,m;
    var dd = date.getDate();
    var mm = date.getMonth() + 1;
    var yyyy = date.getFullYear();
    if (dd < 10) { dd = dd }
    if (mm < 10) { mm = mm }
    date = yyyy + '-' + mm + '-' + dd;
    return date
}

export function Last7Days() {
    var result = [];
    for (var i = 0; i < 7; i++) {
        var d = new Date();
        d.setDate(d.getDate() - i);
        result.push(formatDate(d))
    }
    console.log("LAST 7 DAYS " + JSON.stringify(result))
    return result
}

export function getColor(index) {
    return LineChartColor[index];
}



export function getTransactionArray(transactionArray) {
    var length = transactionArray.length;
    for (var index = length; index < 5; index++) {
        var data = {
            "id": "-",
            "receiptId": "-",
            "transactionId": "-",
            "step": getTitle(index),
            "status":"-"
        }

        transactionArray.push(data)
    }
    return transactionArray;
}


const STEPS = ['INIT_PROPOSAL',
'CREATE_PO',
'SEND_INVOICE',
'MAKE_PAYMENT',
'CREATE_RECEIPT',
'CANCEL']


export function getIndex(title){
    var index=STEPS.indexOf(title)
    if (index < 0 )
    return 0
    else
    return index
}

export const goToTop = () => {
    document.getElementsByClassName("ui-container")[0].scrollTo(0, 0)
}

